.loader-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 30vh;
  display: flex;
}

.loader {
  width: 70px;
  height: 70px;
  position: relative;
}

.loader:before {
  content: "";
  border: 6px solid #007bff;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  animation: 1s ease-in-out infinite pulse;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  content: "";
  border: 6px solid #0000;
  border-top-color: #007bff;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  animation: 2s linear infinite spin;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-text {
  color: #007bff;
  text-align: center;
  text-transform: uppercase;
  margin-top: 20px;
  font-family: Arial, sans-serif;
  font-size: 24px;
}

@keyframes pulse {
  0% {
    opacity: 1;
    transform: scale(.6);
  }

  50% {
    opacity: 0;
    transform: scale(1.2);
  }

  100% {
    opacity: 1;
    transform: scale(.6);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.content, .loaded .loader-container {
  display: none;
}

.loaded .content {
  display: block;
}

